































import { Content, db } from "@/fakeDB";
import Vue from "vue";
type LoadingContent = {
  status: "Loading";
};
type ErrorContent = {
  status: "Error";
};
type LoadedContent = {
  status: "Complete";
  content: Content;
};

type DetailsData = {
  current: LoadingContent | ErrorContent | LoadedContent;
};

export default Vue.extend({
  data() {
    return {
      current: { status: "Loading" },
    } as DetailsData;
  },
  created() {
    this.loadContent();
  },
  methods: {
    loadContent() {
      setTimeout(() => {
        if (db[this.$route.params.uuid] === undefined) {
          this.current.status = "Error";
        } else {
          this.current = {
            status: 'Complete',
            content: db[this.$route.params.uuid]
          }
        }
      }, 1000);
    },
  },
});
